import CardDota from "@/components/utils/cards/Card";
import useAxios from "@/hooks/useAxios";
import loading from "@/layouts/loading";
import axiosInstance from "@/services";
import {
  Button,
  Card,
  CardBody,
  Form,
  Input,
  InputOtp,
  Select,
  SelectItem,
  Spinner,
} from "@heroui/react";
import dayjs from "dayjs";
import { FC, useMemo, useState } from "react";
import toast from "react-hot-toast";
import { useTranslation } from "react-i18next";
import { History } from "../../invesment/MyInvestments";

type Props = {
  refetchHistory: () => void;
  enabled: boolean;
};

const DepositWithdrawal: FC<Props> = ({ refetchHistory, enabled }) => {
  const i18n = useTranslation();
  const [loading, setLoading] = useState(false);
  const [amount, setAmount] = useState("");
  const { data: deposits, refetch } = useAxios<History[]>({
    url: "users/deposits",
    method: "get",
  });
  const [depositSelected, setDepositSelected] = useState("");
  const [available, setAvailable] = useState("");
  const [pendingBalance, setPendingBalance] = useState("");
  const [activating, setActivating] = useState(false);

  const exectWithdraw = async (otp: FormDataEntryValue | null) => {
    if (!otp) return toast.error("No Existe el otp");
    if (Number(amount) > Number(available))
      return toast.error("La cantidad excede el líminite disponible");
    setLoading(true);
    try {
      const request = await axiosInstance.post("users/create-withdraw", {
        amount: Number(amount),
        otp,
        type: "deposit",
        deposit_id: depositSelected,
      });
      toast.success("Retiro solicitado con éxito");
      refetch();
      refetchHistory();
      setLoading(false);
      setAmount("");
      setActivating(false);
    } catch (error: any) {
      toast.error(
        `No se pudo realizar el retiro: ${error?.response?.data.message}`
      );
      console.log("fallo al realizar el retiro", error);
      setLoading(false);
    }
  };

  const requestOtp = async () => {
    try {
      await axiosInstance.post("users/otp");
      toast.success("Se envió el código de verificación a tu correo");
      setActivating(true);
    } catch (error) {
      console.log("Fallo al solicitar el otp", error);
    }
  };

  const depositsWithKeys = deposits?.map((de) => ({
    key: de.id,
    label: de.amount.toString(),
    rewards_balance: de.rewards_balance,
    amount: de.amount,
    rewards_pending: de.rewards_pending,
  }));

  const handleSelection = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedKey = e.target.value;
    const selectedDeposit = depositsWithKeys?.find(
      (de) => de.key === selectedKey
    );
    if (selectedDeposit) {
      setDepositSelected(selectedKey);
      setAvailable(selectedDeposit?.rewards_balance?.toString());
      setPendingBalance(selectedDeposit?.rewards_pending?.toString());
    }
  };

  if (!depositsWithKeys) return <Spinner />;

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <CardDota classNames="bg-primary flex flex-col items-center gap-2">
        <span className="text-xl">
          {i18n.t("witdhdrawals_view.available_balance")}
        </span>
        <span className="text-[38px] font-bold">$ {available || 0}</span>
        <Select
          size="lg"
          onChange={handleSelection}
          selectedKeys={[depositSelected]}
        >
          {depositsWithKeys.map((de) => {
            if (de.rewards_balance == 0) return null;
            return (
              <SelectItem key={de.key} value={de.key}>
                {de.label}
              </SelectItem>
            );
          })}
        </Select>
        <Input
          startContent={"$"}
          label={i18n.t("witdhdrawals_view.amount_to_withdraw")}
          color="secondary"
          type="number"
          value={amount}
          max={Number(available)}
          onChange={(e) => setAmount(e.target.value)}
          endContent="USD"
        />
        {activating && (
          <Card className="w-full">
            <CardBody className="flex w-full flex-col gap-2">
              <Form
                validationBehavior="native"
                className="w-full flex items-center flex-col"
                onSubmit={(e) => {
                  e.preventDefault();
                  const formData = new FormData(e.currentTarget);
                  const otp = formData.get("otp");
                  exectWithdraw(otp);
                }}
              >
                <span>Introduce el código de verificación</span>
                <InputOtp
                  isRequired
                  length={4}
                  name="otp"
                  size="lg"
                  placeholder="Introduce el código"
                  validationBehavior="native"
                />

                <Button
                  size="lg"
                  variant="bordered"
                  color="default"
                  className="w-full border-white"
                  isLoading={loading}
                  disabled={loading}
                  type="submit"
                >
                  Verficar
                </Button>
              </Form>
            </CardBody>
          </Card>
        )}
        <Button
          size="lg"
          variant="bordered"
          color={enabled ? "default" : "danger"}
          className={`w-full ${enabled && "border-white"}`}
          isLoading={activating}
          disabled={activating || !enabled}
          onPress={enabled ? requestOtp : () => {}}
        >
          {i18n.t("witdhdrawals_view.withdraw")}
        </Button>
      </CardDota>
      <CardDota classNames="bg-blue-500 justify-center flex flex-col items-center gap-2">
        <span className="text-xl">
          {i18n.t("witdhdrawals_view.pending_balance")}
        </span>
        <span className="text-[38px] font-bold">$ {pendingBalance || 0}</span>
      </CardDota>
    </div>
  );
};

export default DepositWithdrawal;
