import ButtonDota from "@/components/utils/cards/ButtonDota";
import CardDota from "@/components/utils/cards/Card";
import TableDota from "@/components/utils/cards/TableDota";
import useAxios from "@/hooks/useAxios";
import axiosInstance from "@/services";
import { Button, Card, CardBody, Input, Tab, Tabs } from "@heroui/react";
import dayjs from "dayjs";
import "dayjs/locale/es";
import { useState } from "react";
import toast from "react-hot-toast";
import { useTranslation } from "react-i18next";
import { BiMoneyWithdraw } from "react-icons/bi";
import { BsCash } from "react-icons/bs";
import DirectWithdrawal from "./components/DirectWithdrawal";
import RankWithdrawal from "./components/RankWithdrawal";
import BinanyWithdrawal from "./components/BinaryWithdrawal";
import DepositWithdrawal from "./components/DepositWithdrawal";
dayjs.locale("es");

const Retiros = () => {
  const i18n = useTranslation();
  const cols = [
    { header: i18n.t("witdhdrawals_view.amount"), accessor: "amount" },
    {
      header: i18n.t("witdhdrawals_view.date"),
      accessor: "created_at",
      render: (value: string) =>
        `${dayjs(value).format("D [de] MMMM [del] YYYY [a las] hh:mm:ss A")}`,
    },
    {
      header: i18n.t("witdhdrawals_view.status"),
      accessor: "status",
      render: (value: string) => (
        <span className={value == "pending" ? "text-primary" : "text-success"}>
          {value}
        </span>
      ),
    },
  ];

  const enabled =
    dayjs().day() == 4 && dayjs().hour() >= 7 && dayjs().hour() <= 17;

  const { data: history, refetch: refetchHistory } = useAxios<
    { amount: number; created_at: string; status: string }[]
  >({ url: "users/withdraw-history", method: "get" });

  const rows: any =
    history?.map((hist) => ({
      amount: hist.amount,
      created_at: hist.created_at,
      status: hist.status,
    })) || [];

  return (
    <div className="flex flex-col gap-12">
      <div className="flex flex-col gap-4">
        <Tabs>
          <Tab key="direct" title={i18n.t('witdhdrawals_view.tabs.direct')}>
            <Card>
              <CardBody>
                <DirectWithdrawal
                  refetchHistory={refetchHistory}
                  enabled={enabled}
                />
              </CardBody>
            </Card>
          </Tab>
          <Tab key="ranks" title={i18n.t('witdhdrawals_view.tabs.ranks')}>
            <Card>
              <CardBody>
                <RankWithdrawal
                  refetchHistory={refetchHistory}
                  enabled={enabled}
                />
              </CardBody>
            </Card>
          </Tab>
          <Tab key="binary" title={i18n.t('witdhdrawals_view.tabs.binary')}>
            <Card>
              <CardBody>
                <BinanyWithdrawal
                  refetchHistory={refetchHistory}
                  enabled={enabled}
                />
              </CardBody>
            </Card>
          </Tab>
          <Tab key="deposits" title={i18n.t('witdhdrawals_view.tabs.deposits')}>
            <Card>
              <CardBody>
                <DepositWithdrawal
                  refetchHistory={refetchHistory}
                  enabled={enabled}
                />
              </CardBody>
            </Card>
          </Tab>
        </Tabs>
      </div>
      <TableDota
        columns={cols}
        icon={() => <BiMoneyWithdraw />}
        rows={rows}
        tableName={i18n.t("witdhdrawals_view.withdrawal_history")}
      />
    </div>
  );
};

export default Retiros;
